import request from '@/utils/request'
import config from '@/utils/config'

/* 1 */
export function getNewsList(data) {
  const { time, page } = data
  return new Promise((resolve, reject) => {
    request({
      method: 'get',
      url: `${config.news.listUrl}?time=${time}&page=${page}`
    }).then(
      // 
      res => {
        resolve(res)
      },
      // 
      err => {
        reject(err)
      }
    )
  })
}

